.toggle-button {
  position: fixed;
  top: 20px;
  left: 20px;
  z-index: 1000;
  padding: 10px 15px;
  background-color: #72AB64;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.toggle-button:hover {
  background-color: #4135AB;
}

/* Contenu avec le menu visible */
.with-sidebar {
  margin-left: 0px;
}

/* Contenu plein écran quand le menu est masqué */
.full-width {
  margin-left: 0;
  width: 100%;
}

/* Styles pour une mise en page réactive */
.app-container {
  display: flex;
}

.content {
  padding: 50px;
  width: 100%;
}



