/* src/EquipmentsPage.css */

.equipments-page {
  padding: 20px;
  background-color: #f4f4f9;
}

.equipments-page h2 {
  color: #333;
}

.equipments-list {
  list-style: none;
  padding: 0;
}

.equipment-item {
  padding: 15px;
  background-color: #e3f2fd;
  border: 1px solid #90caf9;
  margin-bottom: 10px;
  border-radius: 5px;
  font-size: 16px;
}

.equipment-item strong {
  color: #1565c0;
}
